<template>
	<div class="content">
          <h3 style="padding:15px;">辅导班学员常见问题解答</h3>
		<div v-for="q in Qs">
            <div class="panel panel-default">
                <!-- Default panel contents -->
                <div class="panel-heading" style="padding:5px;"><p style="color:#2278cf;margin:5px;">{{q.Q}}</p></div>
                <div class="panel-body" style="padding:5px;">
                  <p style="margin:5px;">{{q.A}}</p>
                </div>
            </div>
          </div>
	</div>
</template>

<script>
	export default{
		name:"faq",
		data(){
			return{
                  Qs:[]
			}
		},
          created(){
            this.$http.get('./../static/faq.json')
                .then(function(data){
                  // console.log(data);
                  return data.json();
                })
                .then(function(data){
                  this.Qs = data;
                  // console.log(this.students);
                })
          }
	}
</script>

<style scoped>
	article{
		padding: 20px 0 40px 0;
		box-sizing: border-box;
	}
	h1{
		color: #093f70;
		padding-top: 60px;
	}
  .content{
    padding-top: 30px;
    padding-bottom: 20px;
  }
  .iconfont-book{
    font-family: iconfont;
    font-size: 100px;
  }
</style>


